<template>
  <div class="content">
    <div class="md-layout">
      <div
        class="md-layout-item md-medium-size-70 md-xsmall-size-70 md-size-70"
      >
        <div
          v-show="!show_form_add"
          class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        >
          <md-card class="md-card-plain">
            <md-card-header data-background-color="blue">
              <h4 class="title">ACCOUNT</h4>
            </md-card-header>
            <md-card-content>
              <account-table
                :accounts="accounts"
                :instances="instances"
                v-on:click_an_account="click_an_account($event)"
              ></account-table>
            </md-card-content>
          </md-card>
        </div>
        <div
          v-show="!show_form_add"
          class="md-layout-item md-size-100 text-left"
        >
          <md-button
            class="md-raised md-success"
            id="btn-add-account"
            @click="show_form_add = true"
            >Add Account</md-button
          >
        </div>
        <div
          v-show="show_form_add"
          class="form-add-account md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        >
          <md-card class="md-card-plain">
            <md-card-header data-background-color="orange">
              <h4 class="title">Add ACCOUNT</h4>
            </md-card-header>
            <md-card-content>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Account name</label>
                  <md-input v-model="name_account" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>AccessKeyId</label>
                  <md-input v-model="new_access_key" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>SecretKeyId</label>
                  <md-input
                    v-model="new_secret_access_key"
                    type="text"
                  ></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Region</label>
                  <md-input v-model="new_region" type="text"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 text-center">
                <md-button
                  id="btn-add"
                  class="md-raised md-success"
                  @click="click_accept_add"
                  >Add</md-button
                >
                <md-button
                  v-show="show_form_add"
                  id="btn-close"
                  class="md-raised md-danger"
                  @click="show_form_add = false"
                  >Close</md-button
                >
              </div>
            </md-card-content>
          </md-card>
        </div>
      </div>
      <div
        class="md-layout-item md-medium-size-30 md-xsmall-size-30 md-size-30"
      >
        <md-card class="md-card-plain">
          <md-card-header data-background-color="green">
            <h4 class="title">Instance</h4>
          </md-card-header>
          <md-card-content>
            <instance-table
              :instances_choosed="instances_choosed"
            ></instance-table>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import { AccountTable, InstanceTable } from "@/components";
import url from "../../config/url";
import { post, get } from "../axios.js";
export default {
  components: {
    AccountTable,
    InstanceTable
  },
  data() {
    return {
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
      name_account: "",
      new_access_key: "",
      new_secret_access_key: "",
      new_region: "",
      accounts: [],
      instances: [],
      instances_choosed: [],
      username: localStorage.getItem("username") || "",
      email: localStorage.getItem("email") || "",
      show_form_add: false
    };
  },

  methods: {
    notifyVue(top_or_down, right_or_left, message, iColor) {
      var color = iColor;

      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: right_or_left,
        verticalAlign: top_or_down,
        type: this.type[color]
      });
    },

    click_accept_add: function() {
      let account = {
        name: this.name_account,
        accessKeyId: this.new_access_key,
        secretAccessKey: this.new_secret_access_key,
        region: this.new_region,
        email: this.email
      };

      this.verifyAccountAws(account);
    },

    verifyAccountAws: async function(account) {
      var api = url.url + "/api/accountaws/add";
      const data = await post(api, account);

      if (data.success) {
        this.getListAccount();
        this.notifyVue("top", "right", "Thm ti khon thnh cng", 2);
      } else {
        this.notifyVue("top", "right", "Thm ti khon tht bi", 4);
      }
    },

    click_an_account: function(accountId) {
      this.instances_choosed = [];
      /* istanbul ignore next */
      this.instances.forEach(instance => {
        if (instance.idAccount === accountId)
          this.instances_choosed.push(instance);
      });
    },

    getListAccount: async function() {
      const api = url.url + "/api/accountaws?email=" + this.email;

      const data = await get(api);

      /* istanbul ignore else */
      if (data.success) {
        this.accounts = data.data;

        this.accounts.forEach(async account => {
          await this.getListInstance(account);
        });
      } else if (data.success === false) {
        this.notifyVue("top", "right", "Gp li, hy th ng nhp li", 4);
      } else this.notifyVue("top", "right", "Server khng phn hi", 4);
    },

    getListInstance: async function(account) {
      const api = url.url + "/api/vms";
      const data = await post(api, { id: account._id });

      if (data.success) {
        for (let i = 0; i < data.data.length; i++) {
          data.data[i].idAccount = account._id;
          this.instances.push(data.data[i]);
        }
      } else {
        this.notifyVue("top", "right", "C li khi load instance", 4);
      }
    }
  },

  mounted: function() {
    this.getListAccount();
  }

  // watch: {
  //   pagination: function() {
  //     this.getAccountsShow(this.pagination.current_page);
  //   }
  // }
};
</script>
