<template>
  <div>
    <div
      :table-header-color="tableHeaderColor"
      :style="{ width: '100%', height: '500px', overflow: 'auto' }"
    >
      <md-table v-model="plans_show">
        <md-table-row
          slot="md-table-row"
          slot-scope="{ item }"
          :class="[plan_choosing(item) ? 'plan-clicked' : '']"
          @click="emit_event_click_row(item)"
        >
          <md-table-cell md-label="Type">{{ item.typePlan }}</md-table-cell>
          <md-table-cell md-label="Owner">
            {{ item.valueObjectPlan }}
          </md-table-cell>
          <md-table-cell md-label="Date">{{ item.date }}</md-table-cell>
          <md-table-cell md-label="Time">{{ item.time }}</md-table-cell>
          <md-table-cell md-label="Action">{{ item.action }}</md-table-cell>
          <md-table-cell md-label="Daily">{{ item.schedule }}</md-table-cell>
        </md-table-row>
      </md-table>
    </div>
    <pagination
      v-bind:pagination="pagination"
      v-on:click.native="getPlanShow(pagination.current_page)"
      :offset="4"
    ></pagination>
  </div>
</template>

<script>
import Pagination from "../Pagination.vue";
export default {
  name: "plan-table",
  components: {
    Pagination
  },
  props: {
    tableHeaderColor: {
      type: String,
      default: ""
    },
    plans: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      selected: [],
      plan_active: 0,
      page: 1,
      perpage: 9,
      pages: [],
      pagination: {
        total: 1,
        per_page: 2,
        last_page: 0,
        from: 1,
        to: 1,
        current_page: 1,
        counts: 8
      },
      offset: 4,

      plans_show: []
    };
  },

  methods: {
    emit_event_click_row: function(plan) {
      this.$emit("click_a_plan", plan);
      this.plan_active = plan;
    },

    plan_choosing: function(plan) {
      if (this.plan_active._id === plan._id) {
        return true;
      }

      return false;
    },

    getPlanShow: function(current_page) {
      this.plans_show = [];
      for (let i = 0; i < this.pagination.counts; i++) {
        const index =
          current_page * this.pagination.counts - this.pagination.counts + i;

        if (index < this.plans.length) {
          this.plans_show.push(this.plans[index]);
        }
      }
    },

    setPages() {
      let numberOfPages = Math.ceil(this.plans.length / this.page);

      /* istanbul ignore next */
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    }
  },

  watch: {
    plans: function() {
      this.pagination.last_page =
        this.plans.length / this.pagination.counts + 1;
      this.getPlanShow(this.pagination.current_page);
    }
  }
};
</script>
<style scoped>
.plan-clicked {
  background-color: rgb(182, 179, 179);
}
</style>
