<template>
  <div class="content">
    <div class="md-layout">
      <div
        class="md-layout-item md-medium-size-60 md-xsmall-size-60 md-size-60"
      >
        <div class="md-layout">
          <div class="md-layout-item md-small-size-25 md-size-25">
            <md-field>
              <label>Search by Account, Key name</label>
              <md-input
                type="text"
                v-model="text_search"
                v-on:keyup.enter="doSearch"
              ></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-25 md-size-25">
            <md-button class="md-raised md-danger" @click="click_delete"
              >Delete</md-button
            >
          </div>
        </div>
        <div
          class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-100"
        >
          <md-card class="md-card-plain">
            <md-card-header style="background-color:#29a3a3">
              <h4 class="title">PLAN</h4>
            </md-card-header>
            <md-card-content>
              <plan-table
                :plans="plans_show"
                v-on:click_a_plan="click_a_plan($event)"
              ></plan-table>
            </md-card-content>
          </md-card>
        </div>
      </div>
      <div
        class="md-layout-item md-medium-size-40 md-xsmall-size-40 md-size-40"
      >
        <md-card class="md-card-plain">
          <md-card-header style="background-color:#7979d2">
            <h4 class="title">Add Plan</h4>
          </md-card-header>
          <md-card-content>
            <div class="md-layout-item md-small-size-100 md-size-100">
              <div>
                <input
                  type="radio"
                  v-model="typePlan"
                  name="type_plan"
                  value="instance_id"
                />
                InstanceId
                <br />
                <input
                  type="radio"
                  v-model="typePlan"
                  name="type_plan"
                  value="account_name"
                />
                AccountName
                <br />
                <input
                  type="radio"
                  v-model="typePlan"
                  name="type_plan"
                  value="key_name"
                />
                KeyName
                <br />
              </div>
              <md-field>
                <label>{{ typePlan }}</label>
                <md-input type="text" v-model="valueObjectPlan">{{
                  typePlan
                }}</md-input>
              </md-field>
              <md-field v-show="typePlan === 'instance_id'">
                <label>Account name</label>
                <md-input type="text" v-model="accountName"
                  >Account name</md-input
                >
              </md-field>
            </div>
            <div>
              <input
                type="radio"
                v-model="action"
                name="action"
                value="start"
              />
              Start
              <br />
              <input type="radio" v-model="action" name="action" value="stop" />
              Stop
              <br />
              <br />
            </div>
            <hr />
            <div>
              <br />
              <input
                type="radio"
                v-model="schedule"
                name="daily"
                value="daily"
              />
              Daily
              <br />
              <input
                type="radio"
                v-model="schedule"
                name="daily"
                value="nodaily"
              />
              No daily
              <br />
            </div>
            <div class="md-layout-item md-small-size-100 md-size-100">
              <md-field>
                <md-input type="datetime-local" v-model="dateTime"></md-input>
              </md-field>
            </div>
            <div class="md-layout-item md-size-100 text-center">
              <md-button class="md-raised md-success" @click="click_add_plan"
                >Add</md-button
              >
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>
<script>
import { PlanTable } from "@/components";
import { get, post, deleteAxios } from "../axios.js";
import url from "../../config/url";
export default {
  components: {
    PlanTable
  },

  data: () => {
    return {
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
      action: "start",
      typePlan: "instance_id",
      valueObjectPlan: "",
      dateTime: "",
      plans: [],
      plans_show: [],
      plan_choosed: "",
      accountName: "",
      text_search: "",
      schedule: "daily"
    };
  },

  methods: {
    notifyVue(top_or_down, right_or_left, message, iColor) {
      var color = iColor;

      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: right_or_left,
        verticalAlign: top_or_down,
        type: this.type[color]
      });
    },

    getListPlan: async function() {
      const api = url.url + "/api/vms/plans";
      const data = await get(api);

      /* istanbul ignore else */
      if (data.success) {
        this.plans = data.data;
        this.plans_show = data.data;
      } else {
        this.notifyVue("top", "right", "Server khng phn hi", 4);
      }
    },

    // checkPlan: async function() {
    //   const api = url.url + "/api/vms/check_plan";
    //   const data = await get(api);
    // },

    doSearch: function() {
      this.plans_show = [];

      if (this.text_search.length === 0) {
        this.plans_show = this.plans;
      }
      this.plans.forEach(plan => {
        /* istanbul ignore next */
        if (this.text_search === plan.valueObjectPlan) {
          this.plans_show.push(plan);
        }
      });
    },

    click_add_plan: async function() {
      if (this.valueObjectPlan.length === 0 || !this.dateTime) {
        this.notifyVue(
          "top",
          "right",
          "Hy xem li ty chn, khng b trng",
          3
        );

        return;
      }

      const dateTime = new Date(this.dateTime);

      if (dateTime.getTime() < Date.now()) {
        this.notifyVue(
          "top",
          "right",
          "Thi im ny  xy ra, hy chn 1 thi im trong tng lai",
          3
        );

        return;
      }

      /* istanbul ignore next */
      if (this.typePlan === "instance_id") {
        this.valueObjectPlan = this.valueObjectPlan + "&&" + this.accountName;
      }

      this.dateTime += ":0";
      const dateArr = this.dateTime.split("T");
      var plan = {
        typePlan: this.typePlan,
        valueObjectPlan: this.valueObjectPlan,
        action: this.action,
        date: dateArr[0],
        time: dateArr[1],
        schedule: this.schedule
      };

      const api = url.url + "/api/vms/make-plan";

      await post(api, plan);

      this.getListPlan();
      this.notifyVue("top", "right", " xt plan", 2);
    },

    click_a_plan: function(plan) {
      this.plan_choosed = plan;
    },

    /* istanbul ignore next */
    click_delete: async function() {
      if (!confirm("Ban c mun xa plan ny khng ?")) return;
      const api = url.url + "/api/vms/delete-plan?id=" + this.plan_choosed._id;

      await deleteAxios(api);
      this.notifyVue("top", "right", "Xa thnh cng", 2);
      this.getListPlan();
      // else this.notifyVue("top", "right", "Xa tht bi,  xy ra li", 4);
    }
  },

  mounted: function() {
    this.getListPlan();
    // this.checkPlan();
  }
};
</script>
<style scoped>
small {
  display: block;
}

input[type="radio"] {
  width: 20%;
  font-size: 22px;
  font-weight: bold;
}
</style>
