import axios from "axios";

// let axiosGet = async function(api) {
// };

let get = function(api) {
  const data = axios.get(api).then(result => {
    return result.data;
  });

  return data;
};

let post = function(api, body) {
  const data = axios
    .post(api, body, {
      header: {
        accept: "application/json",
        "accept-language": "en-US",
        "content-type": "application/x-www-form-urlencoded"
      }
    })
    .then(result => {
      return result.data;
    });

  return data;
};

let deleteAxios = function(api) {
  const data = axios.delete(api).then(result => {
    return result.data;
  });

  return data;
};

let putAxios = function(api) {
  const data = axios.put(api).then(result => {
    return result.data;
  });

  return data;
};

export { get, post, deleteAxios, putAxios };
