<template>
  <div class="limiter">
    <div class="container-login100">
      <div class="wrap-login100">
        <div class="login100-pic js-tilt" data-tilt>
          <img src="../../template/Login_v1/images/img-01.png" alt="IMG" />
        </div>
        <div class="login100-form validate-form">
          <span class="login100-form-title">Member Login</span>

          <div
            class="wrap-input100 validate-input"
            data-validate="Valid email is required: ex@abc.xyz"
          >
            <input
              class="input100"
              type="text"
              name="username"
              placeholder="Username "
              v-model="username"
            />
            <span class="focus-input100"></span>
            <span class="symbol-input100">
              <i class="fa fa-user" aria-hidden="true"></i>
            </span>
          </div>

          <div
            class="wrap-input100 validate-input"
            data-validate="Password is required"
          >
            <input
              class="input100"
              type="password"
              name="pass"
              placeholder="Password"
              v-model="password"
            />
            <span class="focus-input100"></span>
            <span class="symbol-input100">
              <i class="fa fa-lock" aria-hidden="true"></i>
            </span>
          </div>

          <div class="container-login100-form-btn">
            <button class="login100-form-btn" @click="click_login">
              Login
            </button>
          </div>

          <div class="text-center p-t-12">
            <span class="txt1">Forgot</span>
            <a class="txt2" href="#">Username / Password?</a>
          </div>

          <div class="text-center p-t-136">
            <a class="txt2" href="#/register">
              Create your Account
              <i class="fa fa-long-arrow-right m-l-5" aria-hidden="true"></i>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import BaseDialog from './dialog/basedialog.vue'
import { post } from "../axios.js";
import url from "../../config/url";
export default {
  name: "Login",

  data: () => {
    return {
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
      username: "",
      password: "",
      show: false,
      title: "Thng bo"
    };
  },

  methods: {
    notifyVue(top_or_down, right_or_left, message, iColor) {
      var color = iColor;

      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: right_or_left,
        verticalAlign: top_or_down,
        type: this.type[color]
      });
    },

    click_login: async function() {
      const api = url.url + "/api/login";

      let user = {
        username: this.username,
        password: this.password
      };

      const data = await post(api, user);

      /* istanbul ignore else */
      if (data.success) {
        localStorage.setItem("username", this.username);
        localStorage.setItem("email", data.email);
        this.$router.push("/");
        this.notifyVue("top", "center", "OK", 2);
      } else if (data.success === false) {
        this.notifyVue(
          "top",
          "center",
          "Ti khon hoc mt khu khng chnh xc",
          4
        );
      } else {
        /* istanbul ignore next */
        this.notifyVue("top", "right", "Khng th kt ni ti server", 4);
      }
    }
  },
  mounted: ()=>{
    console.log("Url: "+ url.url)
  }
};
</script>

