<template>
  <div>
    <div :style="{ width: '100%', height: '500px' }">
      <md-table v-model="instances_show" :table-header-color="tableHeaderColor">
        <md-table-row slot="md-table-row" slot-scope="{ item }">
          <!-- <md-table-cell md-label="AccountName">{{ item.accountName }}</md-table-cell> -->

          <md-table-cell md-label="Account name">
            {{ item.account_name }}
          </md-table-cell>

          <md-table-cell md-label="Name">
            {{
              item.Instances[0].Tags.length > 0
                ? item.Instances[0].Tags[0].Value
                : ""
            }}
          </md-table-cell>
          <md-table-cell md-label="Id">
            {{ item.Instances[0].InstanceId }}
          </md-table-cell>
          <md-table-cell md-label="Status">
            {{ item.Instances[0].State.Name }}
          </md-table-cell>
          <md-table-cell md-label="InstanceType">
            {{ item.Instances[0].InstanceType }}
          </md-table-cell>
          <md-table-cell md-label="ImageId">
            {{ item.Instances[0].ImageId }}
          </md-table-cell>
          <md-table-cell md-label="KeyName">
            {{ item.Instances[0].KeyName }}
          </md-table-cell>
          <md-table-cell md-label="Launch Time">
            {{ item.Instances[0].LaunchTime }}
          </md-table-cell>
          <md-table-cell md-label="Region">
            {{ item.Instances[0].Placement.AvailabilityZone }}
          </md-table-cell>
          <md-table-cell md-label>
            <md-button
              id="btnStart"
              class="md-raised md-success"
              @click="
                click_start(
                  item.Instances[0].InstanceId,
                  item.Instances[0].State.Name,
                  item.idAccount
                )
              "
              >Start</md-button
            >
            <!-- <md-icon style="color:green" @click="click_start(item.Instances[0].InstanceId, item.Instances[0].State.Name)">play_arrow</md-icon>
            <md-tooltip md-direction="top">Start</md-tooltip>-->
          </md-table-cell>
          <md-table-cell md-label>
            <md-button
              class="md-raised md-warning"
              @click="
                click_stop(
                  item.Instances[0].InstanceId,
                  item.Instances[0].State.Name,
                  item.idAccount
                )
              "
              >Stop</md-button
            >
          </md-table-cell>
          <md-table-cell md-label>
            <md-button
              class="md-raised md-danger"
              @click="
                click_remove(item.Instances[0].InstanceId, item.idAccount)
              "
              >Remove</md-button
            >
          </md-table-cell>
        </md-table-row>
      </md-table>
    </div>
    <pagination
      v-bind:pagination="pagination"
      v-on:click.native="getInstancesShow(pagination.current_page)"
      :offset="4"
    ></pagination>
  </div>
</template>

<script>
import url from "../../../config/url";
import { deleteAxios, putAxios } from "../../axios.js";
import Pagination from "../Pagination.vue";

export default {
  name: "instance-full-table",
  components: {
    Pagination
  },

  props: {
    tableHeaderColor: {
      type: String,
      default: ""
    },
    instances: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
      selected: [],

      pagination: {
        total: 1,
        per_page: 2,
        last_page: 1,
        from: 1,
        to: 1,
        current_page: 1,
        counts: 5
      },
      offset: 4,

      instances_show: []
    };
  },
  methods: {
    notifyVue(top_or_down, right_or_left, message, iColor) {
      var color = iColor;

      this.$notify({
        message: message,
        icon: "add_alert",
        horizontalAlign: right_or_left,
        verticalAlign: top_or_down,
        type: this.type[color]
      });
    },

    getInstancesShow: function(current_page) {
      this.instances_show = [];
      // const index = current_page * this.pagination.counts - this.pagination.counts;
      for (let i = 0; i < this.pagination.counts; i++) {
        const index =
          current_page * this.pagination.counts - this.pagination.counts + i;

        if (index < this.instances.length) {
          this.instances_show.push(this.instances[index]);
        }
      }
    },

    click_start: async function(id, status, idAccount) {
      if (!confirm("Bn c mun khi ng instance ny? ")) return;
      if (status === "terminated") {
        this.notifyVue(
          "top",
          "right",
          "Khng th khi ng instance  trng thi terminated",
          4
        );

        return;
      }
      const api =
        url.url + "/api/vms/start/?id=" + id + "&&idAccount=" + idAccount;
      const data = await putAxios(api);

      /* istanbul ignore else */
      if (data.success) {
        this.notifyVue(
          "top",
          "right",
          "ang khi ng instance c id = " + id,
          2
        );
      } else if (data.success === false) {
        this.notifyVue("top", "right", "Khi ng b li", 4);
      } else this.notifyVue("top", "right", "Server khng phn hi", 4);
    },

    click_stop: async function(id, status, idAccount) {
      if (!confirm("Bn c mun dng instance ny? ")) return;
      if (status === "terminated") {
        this.notifyVue(
          "top",
          "right",
          "Khng th dng instance  trng thi terminated",
          4
        );

        return;
      }
      const api =
        url.url + "/api/vms/stop/?id=" + id + "&&idAccount=" + idAccount;
      const data = await putAxios(api);

      if (data.success) {
        this.notifyVue("top", "right", "ang dng instance c id = " + id, 2);
      } else if (data.success === false)
        this.notifyVue("top", "right", "C li xy ra khi dng instance", 4);
      else this.notifyVue("top", "right", "Server khng phn hi", 4);
    },

    click_remove: async function(id, idAccount) {
      const api = url.url + "/api/vms?id=" + id + "&&idAccount=" + idAccount;
      const data = await deleteAxios(api);
      /* istanbul ignore else */

      if (data.success === false) {
        this.notifyVue("top", "right", data.message, 3);
      } else if (data.success) {
        this.notifyVue("top", "right", "Xa thnh cng", 2);
      } else this.notifyVue("top", "right", "Server khng phn hi", 4);
    }

    // compare_instance: function(instance) {
    //   if (
    //     this.instance_choosed.Instances[0].InstanceId ===
    //       instance.Instances[0].InstanceId &&
    //     this.instance_choosed.account_name === instance.account_name
    //   )
    //     return true;

    //   return false;
    // }
  },

  watch: {
    instances: function() {
      this.pagination.last_page =
        this.instances.length / this.pagination.counts + 1;
      // console.log('last page: '+ this.pagination.last_page)\
      this.getInstancesShow(this.pagination.current_page);
    }
  }
};
</script>

<style scoped>
.choosed {
  background-color: gray;
}
</style>
